<style lang="stylus" scoped>
  .smallhead
    align-items center
    height 2rem
    background #e51c23
    color #ffffff
    span 
      flex 0 0 1.2rem
    .menu
      height 0.8125rem
      border-radius 0.16rem
      border solid 0.0208rem #ffffff
      margin-left 0.6875rem
      &:before
        font-size 0.96rem
    .icon
      margin 0 0.68rem
      &:before
        font-size 0.8rem
      &:last-of-type  
        margin-right 0.6rem
    p
      font-size 0.64rem
      flex 1
      text-align center
</style>

<template>
  <div class="smallhead flex">
    <span @click="prev" class="icon icon-Left" v-show="!judge"></span>
    <p>{{title}}</p>
    <q-btn flat dense round aria-label="Menu" class="menu icon icon-menu" @click="menu"/>
    <!-- <span v-show="share" class="icon icon-fenxiang3"></span> -->
  </div>
</template>

<script>
import {mapGetters,mapMutations} from 'vuex';
export default {
  props:{
    // 标题
    title:{
      type:String
    },
    // 两侧显示影藏标识
    share:{
      type:Boolean,
      default:false
    },
    judge:{
      type:String
    }
  },
  computed:{
    ...mapGetters([
      "infos"
    ])
  },
  methods:{
    // 后退
    prev(){
      this.$router.go(-1)
    },
    // 打开菜单
    menu(){
      this.setinfo({menu:true})
    },
    ...mapMutations({
      setinfo:'SET_INFO',
    }),
  }
}
</script>
