<style lang="stylus" scoped>
  .q-item
    font-size 12px !important
</style>


<template>
  <div id="app" class="scroll no-scroll">
    <router-view/>
  </div>
</template>

<script>
import {mapActions,mapMutations,mapGetters} from 'vuex';
import io from "socket.io-client";
import data from '@/common/data/data.json'
import {get} from './api'
import { date } from 'quasar'
export default {
  created(){
    // 添加语言包
    let mydata = {
      Language:data.language,
      index:1
    }
    this.languageAsyn(mydata)
    // 调用可抢红包
    get('/get_money_list').then((val)=>{
      console.log(val)
      let newval = val.data
      let newdata = [[],[],[],[],[],[]]
      for(let i =0;i<newval.length;i++){
        let obj = {
          name:newval[i].name,
          packetId:newval[i].packetId,
          txId:newval[i].txId,
          type:1,
          num:newval[i].num,
          eos:newval[i].eos,
          time:newval[i].time*1000,
          none:newval[i].none,
          isgo:newval[i].isgo
        }
        if(newdata[newval[i].index]){
          newdata[newval[i].index].push(obj)
        }else{
          newdata[newval[i].index][0] = obj
        }
      }
      console.log(newdata)
      this.setpackage(newdata)
    })
  },
  mounted(){
    // 调用socket
    // const socket = io('http://pickown.test:6001');
    const socket = io(window.location.origin);
    let self = this
    socket.on('connect',()=>{});
    // 接收红包广播
    socket.on('issus_packet', (val)=>{
      console.log("接收红包",val)
      // 判断是否是自己发的红包
      if(self.infos.name == val.name){
        return false
      }
      let {out_packet} = val
      let data = {
        index:val.index,
        data:{
          name:val.name,
          packetId:out_packet.eosid,
          txId:out_packet.blocknumber,
          type:1,
          num:out_packet.tail_number,
          eos:out_packet.issus_sum,
          time:out_packet.created_at*1000,
          none:false
        }
      }
      self.setpackage(data)
      // 如果红包不在当前房间，家小点
      if(self.packages.this != val.index){
        self.setinfo([val.index,true])
      }
    })
    // 接收领完消息
    socket.on('income_packet', function(val) {
      console.log(val)
      let item = {}
      // 查找当前领完红包标识
      let tempd = self.packages.data[val.index]
      if(tempd.length>0){
        for(let i=0;i<tempd.length;i++){
          if(tempd[i].packetId == val.out_packet.eosid){
            item = {
              index:val.index,
              index1:i,
              data:{
                none:1
              }
            }
          }
        }
      }
      self.setpackdata(item)
      // 发出信息
      let newlist = {
        index:val.index,
        data:{
          name:val.name,
          in_packet_data:val.in_packet_data,
          reward_data:val.reward_data,
          chailei_data:val.chailei_data,
          type:2
        }
      }
      self.setpackage(newlist)
    })
    socket.on('disconnect', function(){
    });
  },
  methods:{
    ...mapActions([
      'languageAsyn'
    ]),
    ...mapMutations({
      setpackage:'SET_PACKAGE',
      setinfo:'SET_INFO',
      setpackdata:'SET_PACKDATA'
    }),
  },
  computed:{
    ...mapGetters([
      "packages","infos"
    ])
  }
}
</script>

