<style lang="stylus" scoped>
  .recordHair
    max-width 16rem
    margin  0 auto
  .fixed-top
    z-index 1000
  .top
    background url("../common/images/bg3.jpg") no-repeat top
    background-size 100% 100%
    height 10.82rem
    padding-top 5.78rem
    text-align center
    color #222222
    .name
      font-size 0.72rem
    .allprice
      font-size 1.2rem
      margin-top 1.14rem
      font-weight bold
    .num
      margin-top 0.32rem
      font-size 0.48rem
      color #b2b2b2;
      span 
        font-size 0.96rem
  .center
    padding 0 0.8rem
    height 1.38rem
    color #999999
    font-size 0.48rem
    line-height 1.38rem
    background #ffffff
  .bottom
    border-top 0.04rem solid #e8e8e8
    background #ffffff
    li
      border-bottom 0.04rem solid #e8e8e8
      padding 0.68rem 0.8rem
      .info
        justify-content space-between
      .name,.price
        font-size 14px
        color #222222
      .time,.num1
        font-size 0.48rem
        color #999999
        margin-top 0.16rem
      .num1
        text-align right
      .txtinfo
        text-align center
        span
          position relative
          height: 0.82rem;
          line-height 0.82rem;
          background-color: #dddddd;
          display inline-block
          border-radius: 0.2rem;
          font-size 0.48rem
          padding 0 0.28rem 0 0.7rem
          color: #ffffff;
          margin-left 0.4rem
          z-index 0
        .img1
          width: 0.82rem;
          height: 0.68rem;
          position absolute
          top 0.04rem
          left 0.12rem
          z-index 0
  .bottomtxt
    text-align center
    margin-top 0.8rem
</style>

<template>
  <div class="recordHair fullscreen scroll">
    <record-head :title="thislang.hong" class="fixed-top"></record-head>
    <div class="top">
      <p class="name">{{data.outpacketname}}{{thislang.issued}}</p>
      <p class="allprice">{{data.outpacketsum}}EOS</p>
      <p class="num">{{thislang.cai}} <span>{{data.outpackettailnumber}}</span>！</p>
    </div>
    <div class="center" v-if="data.data&&data.data.length>0">{{thislang.lin}}10/10</div>
    <div class="center" v-else>{{thislang.lin}}-/10</div>
    <ul class="bottom" v-if="data.data&&data.data.length>0">
      <li :key="index" v-for="(item,index) in data.data">
        <div class="info flex">
          <div class="left">
            <p class="name">{{item.user}}</p>
            <p class="time">{{item.created_at}}</p>
          </div>
          <div class="right">
            <p class="price">{{item.income_sum}}EOS</p>
            <p class="num1"></p>
          </div>
        </div>
        <p v-if="item.reward_type>0" class="txtinfo"><span>{{thislang.zhong}}：{{typetxt[item.reward_type]}}，{{thislang.huo}}0.0162 EOS</span></p>
        <p v-if="item.is_chailei==1" class="txtinfo"><span><img class="img1" src="../common/images/lei.png">{{thislang.steptitle}}，{{thislang.fu}}{{data.outpacketsum}} EOS</span></p>
      </li>
    </ul>
    <div class="bottomtxt" v-else>抢红包正在进行中，<br />抢完才会公布结果哟！</div>
    
  </div>
</template>

<script>
import recordHead from '@/components/recordHead.vue'
import {mapGetters} from 'vuex';
import {post} from '../api'
import { date } from 'quasar'
export default {
  created(){
    // 获取红包id
    this.packetId = this.$route.params.id
    let data = {
      token:this.$q.sessionStorage.get.item('token'),
      userid:this.$q.sessionStorage.get.item('userid'),
      outid:Number(this.packetId)
    }
    // 获取当前红包抽奖信息
    post('/red_packet',data).then((obj)=>{
      console.log(obj)
      this.data = obj
      this.data.data = obj.data.map((val,i)=>{
        return {
          ...val,
          created_at:date.formatDate(val.updated_at*1000, 'HH:mm:ss')
        }
      })
    }).catch(()=>{
      // console.log(e)
    })
  },
  data(){
    return{
      packetId:'', //红包id
      data:{}, //红包数据
    }
  },
  components: {
    "record-head":recordHead,
  },
  computed:{
    ...mapGetters([
      "thislang"
    ]),
    // 红包获奖状态判断
    typetxt(){
      return ['',this.thislang.dui,this.thislang.san,this.thislang.small,this.thislang.zhen,this.thislang.shun,this.thislang.si,this.thislang.big]
    },
  }
}
</script>

